<template>
<div class="nav-wrapper">
    <div class="sl-nav">
        <flag :iso="locale" class="flag_icon"></flag>&nbsp;
        <ul>
        <li><b>{{ currentLocale }}</b> <i class="fa fa-angle-down" aria-hidden="true"></i>
            <div class="triangle"></div>
            <ul>
                <li @click="setLocale('uz', 'Uzbek')"><flag iso="uz"></flag><span class="active">Uzbek</span></li>
                <li @click="setLocale('ru', 'Русский')"><flag iso="ru"></flag><span>Русский</span></li>
                <li @click="setLocale('us', 'English')"><flag iso="us"></flag><span>English</span></li>
            </ul>
        </li>
        </ul>
    </div>
</div>
</template>
<script>

export default ({
    data:() => ({
        currentLocale: 'Русский',
        locale: null
    }),
    mounted() {
        this.locale = this.$i18n.locale
    },
    methods: {
        setLocale(locale, selectedLang) {
            this.currentLocale = selectedLang
            this.locale = locale;
            import(`../locale/${locale}.json`).then((msgs) => {
                this.$i18n.setLocaleMessage(locale, msgs);
                this.$i18n.locale = locale;
            });
        },
    }
})
</script>

<style scoped>
